import { useCore } from "@/hooks";
import { defineComponent } from "vue";

export default defineComponent({
	name: "cl-refresh-btn",

	setup(props, { slots }) {
		const { crud } = useCore();

		return () => {
			return (
				<el-button
					size="mini"
					onClick={() => {
						crud.refresh();
					}}
					{...props}>
					{slots.default ? slots.default() : crud.dict.label.refresh}
				</el-button>
			);
		};
	}
});
