import { crudList } from "./components/crud/app";
import { Crud } from "./types";
import { Emitter, EmitterItem } from "./types/emitter";
import { isObject } from "./utils";

const emitter: Emitter = {
	list: [],
	init(events: any) {
		if (events && isObject(events)) {
			for (const i in events) {
				this.on(i, events[i]);
			}
		} else {
			console.error("Events 参数格式错误");
		}
	},
	send(name: string, data: any) {
		this.list.forEach((e: EmitterItem) => {
			const [_name, _] = e.name.split("-");

			if (name == _name) {
				e.callback(data, {
					crudList,
					refresh(params: any) {
						crudList.forEach((c: Crud) => c.refresh(params));
					}
				});
			}
		});
	},
	on(name: string, callback: any) {
		this.list.push({ name, callback });
	}
};

export default emitter;
