import { useCore, useForm } from "@/hooks";
import { cloneDeep } from "@/utils";
import { defineComponent, ref } from "vue";

export default defineComponent({
	name: "cl-filter-group",

	componentName: "ClFilterGroup",

	emits: ["search", "reset"],

	props: {
		// 表单值
		modelValue: {
			type: Object,
			default: () => {
				return {};
			}
		},

		// 搜索时钩子, data, { next }
		onSearch: Function
	},

	setup(props, { emit }) {
		const { crudConf, crud } = useCore();
		const { setFormData } = useForm(props);

		// 旧数据
		const oldForm: any = cloneDeep(props.modelValue);

		// 表单数据
		const form: any = setFormData();

		// 加载中
		const loading = ref<boolean>(false);

		function search() {
			const next = (params?: any) => {
				loading.value = true;

				crud.refresh({
					...form,
					page: 1,
					...params
				});

				loading.value = false;
			};

			if (props.onSearch) {
				props.onSearch(form, { next });
			} else {
				next();
			}
		}

		function reset() {
			for (const i in form) {
				form[i] = oldForm[i] === undefined ? undefined : oldForm[i];
			}

			search();
			emit("reset");
		}

		return {
			crudConf,
			form,
			loading,
			search,
			reset
		};
	},

	render(ctx: any) {
		return (
			<div class="cl-filter-group">
				<div class="cl-filter-group__items">{ctx.$slots.default()}</div>

				<div class="cl-filter-group__op">
					<el-button
						{...{
							type: "primary",
							size: "mini",
							...ctx.crudConf.style.filterSearchBtn,
							loading: ctx.loading,
							onClick: () => {
								ctx.search();
							}
						}}>
						搜索
					</el-button>
					<el-button size="mini" onClick={ctx.reset}>
						重置
					</el-button>
				</div>
			</div>
		);
	}
});
