import { defineComponent, h, nextTick, PropType, ref } from "vue";
import { useForm, useCore, useTools, useRefs } from "@/hooks";
import { cloneDeep } from "@/utils";
import { renderNode } from "@/utils/vnode";
import { Form, FormItem } from "@/types";
import { useFormApi } from "./form/helper";

export default defineComponent({
	name: "cl-adv-search",

	props: {
		// 绑定值
		modelValue: {
			type: Object,
			default: () => {
				return {};
			}
		},
		// 表单项
		items: {
			type: Array as PropType<Array<FormItem>>,
			default: () => []
		},
		// el-drawer 参数
		props: {
			type: Object,
			default: () => {
				return {};
			}
		},
		// 操作按钮 ['search', 'reset', 'clear', 'close']
		opList: {
			type: Array,
			default: () => ["close", "search"]
		},
		// 打开钩子 { data, { next } }
		onOpen: Function,
		// 关闭钩子 { done }
		onClose: Function,
		// 搜索钩子 { data, { next, close } }
		onSearch: Function
	},

	emits: ["update:modelValue", "open", "opened", "close", "closed", "reset", "clear"],

	setup(props, { emit }) {
		const { refs, setRefs } = useRefs();
		const { browser } = useTools();
		const { crud, mitt } = useCore();
		const { setFormData } = useForm(props);

		// 表单数据
		const form = setFormData();

		// 是否可见
		const visible = ref<boolean>(false);

		// 表单动作
		const events: any = useFormApi({ refs });

		// 打开
		function open() {
			visible.value = true;

			nextTick(function () {
				const options: Form = {
					items: props.items,
					op: {
						hidden: true
					},
					isReset: false
				};

				refs.value.form.open(options);
			});
		}

		// 打开动画结束
		function onOpened() {
			emit("opened");
		}

		// 关闭
		function close() {
			const done = () => {
				visible.value = false;
			};

			if (props.onClose) {
				props.onClose(done);
			} else {
				done();
			}
		}

		// 关闭回调
		function onClose2() {
			emit("close");
		}

		// 关闭动画结束
		function onClosed() {
			emit("closed");
		}

		// 重置数据
		function reset() {
			events.resetFields();
			emit("reset");
		}

		// 清空数据
		function clear() {
			for (const i in form) {
				form[i] = undefined;
			}
			events.clearValidate();
			emit("clear");
		}

		// 搜素请求
		function search() {
			const params = cloneDeep(form);

			const next = (params: any) => {
				crud.refresh({
					...params,
					page: 1
				});

				close();
			};

			if (props.onSearch) {
				props.onSearch(params, { next, close });
			} else {
				next(params);
			}
		}

		// 消息事件
		mitt.on("crud.openAdvSearch", open);

		return {
			browser,
			refs,
			visible,
			form,
			setRefs,
			open,
			onOpened,
			close,
			onClose2,
			onClosed,
			reset,
			clear,
			search,
			...events
		};
	},

	render(ctx: any) {
		// 渲染表单
		function renderForm() {
			return h(
				<cl-form ref={ctx.setRefs("form")} v-model={ctx.form} inner></cl-form>,
				{},
				ctx.$slots
			);
		}

		// 渲染底部
		function renderFooter() {
			const btns: any = {
				search: "搜索",
				reset: "重置",
				clear: "清空",
				close: "取消"
			};

			return ctx.opList.map((e: any) => {
				if (btns[e]) {
					return (
						<el-button
							{...{
								size: ctx.props.size || "small",
								type: e === "search" ? "primary" : null,
								onClick: ctx[e]
							}}>
							{btns[e]}
						</el-button>
					);
				} else {
					return renderNode(e, {
						scope: ctx.form,
						slots: ctx.$slots
					});
				}
			});
		}

		return (
			<div class="cl-adv-search">
				<el-drawer
					v-model={ctx.visible}
					title="高级搜索"
					direction="rtl"
					size={ctx.browser.isMini ? "100%" : ctx.props.size || "30%"}
					{...{
						onOpened: ctx.onOpened,
						onClosed: ctx.onClosed,
						onClose: ctx.onClose2,
						...ctx.props
					}}>
					<div class="cl-adv-search__container">{renderForm()}</div>
					<div class="cl-adv-search__footer">{renderFooter()}</div>
				</el-drawer>
			</div>
		);
	}
});
