import { useCore } from "@/hooks";
import { defineComponent } from "vue";

export default defineComponent({
	name: "cl-add-btn",

	props: {
		props: Object
	},

	setup(props, { slots }) {
		const { crud } = useCore();

		return () => {
			return (
				crud.getPermission("add") && (
					<el-button
						size="mini"
						type="primary"
						onClick={() => {
							crud.rowAdd();
						}}
						{...props}>
						{slots.default ? slots.default() : crud.dict.label.add}
					</el-button>
				)
			);
		};
	}
});
