import { defineComponent, h, ref } from "vue";
import type { PropType } from "vue";
import { useForm, useCore, useRefs } from "@/hooks";
import { UpsertItem } from "@/types";
import { provide } from "vue";
import { useFormApi } from "../form/helper";

export default defineComponent({
	name: "cl-upsert",

	props: {
		// 绑定值
		modelValue: {
			type: Object,
			default: () => {
				return {};
			}
		},
		// 表单项
		items: {
			type: Array as PropType<UpsertItem[]>,
			default: () => []
		},
		// el-form 参数
		props: {
			type: Object,
			default: () => {
				return {};
			}
		},
		// 编辑时是否同步打开
		sync: Boolean,
		// 操作按钮参数 { hidden, saveButtonText, closeButtonText, buttons }
		op: Object,
		// cl-dialog 参数 { props, hiddenControls, controls }
		dialog: Object,
		// 打开表单钩子 { isEdit, data, { submit, done, close } }
		onOpen: Function,
		// 关闭表单钩子 { done }
		onClose: Function,
		// 获取表单数据钩子 { data, { next, done, close } }
		onInfo: Function,
		// 表单提交钩子 { isEdit, data, { next, done, close } }
		onSubmit: Function
	},

	emits: ["opened", "closed"],

	setup(props, { emit }) {
		const { refs, setRefs } = useRefs();
		const { crud, mitt } = useCore();
		const { setFormData } = useForm(props);

		// 表单数据
		const form: any = setFormData();
		// 是否编辑
		const isEdit = ref<boolean>(false);
		// 数据是否加载完
		const isLoaded = ref<boolean>(false);

		// 关闭表单
		function close() {
			refs.value.form.close();
			isLoaded.value = false;
			emit("closed");
		}

		// 表单关闭前
		function beforeClose() {
			if (props.onClose) {
				props.onClose(close);
			} else {
				close();
			}
		}

		// 提交
		function submit(data: any, event?: any) {
			function done() {
				return event ? event.done() : refs.value.form.done();
			}

			async function next(data: any) {
				// 获取请求方法
				const reqName: string = crud.dict.api[isEdit.value ? "update" : "add"];

				// 验证请求
				if (!crud.service[reqName]) {
					done();
					return Promise.reject(`Request function '${reqName}' is not fount!`);
				}

				// 发送请求
				await crud.service[reqName](data)
					.then((res: any) => {
						ElMessage.success("保存成功");
						close();
						crud.refresh();
						return res;
					})
					.catch((err: string) => {
						ElMessage.error(err);
						Promise.reject(err);
					});

				// 请求完成
				done();
			}

			// 提交钩子
			if (props.onSubmit) {
				props.onSubmit(isEdit.value, data, {
					done,
					next,
					close
				});
			} else {
				next(data);
			}
		}

		// 打开表单
		function open() {
			return new Promise((resolve) => {
				if (!refs.value.form) {
					return false;
				}

				refs.value.form.open({
					title: isEdit.value ? "编辑" : "新增",
					props: props.props,
					items: props.items,
					op: props.op,
					dialog: props.dialog,
					on: {
						open: (_: any, { done, close }: any) => {
							if (props.onOpen) {
								props.onOpen(isEdit.value, form, {
									submit: () => {
										submit(form);
									},
									done,
									close
								});
							}

							resolve(true);
						},
						submit,
						close: beforeClose
					},
					_data: {
						isEdit: isEdit.value
					}
				});
			});
		}

		// 打开后事件
		function onOpened(data: any) {
			isLoaded.value = true;
			emit("opened", isEdit.value, data);
		}

		// 新增
		async function add() {
			isEdit.value = false;
			await open();
			onOpened({});
		}

		// 追加
		async function append(data: any) {
			isEdit.value = false;
			await open();
			if (data) {
				refs.value.form.reBindForm(data);
			}
			onOpened(form);
		}

		// 编辑
		function edit(data?: any) {
			if (!refs.value.form) {
				return false;
			}

			const { showLoading, hiddenLoading } = refs.value.form;

			// 设置为编辑
			isEdit.value = true;

			// 显示加载中
			showLoading();

			// 是否同步打开
			if (!props.sync) {
				open();
			}

			// 完成
			const done = (data?: any) => {
				// 加载完成
				hiddenLoading();

				// 合并数据
				if (data) {
					refs.value.form.reBindForm(data);
				}

				onOpened(form);
			};

			// 关闭表单
			const close = () => {
				hiddenLoading();
				close();
			};

			// 获取详情
			const next = async (data: any) => {
				// 获取请求名称
				const reqName: any = crud.dict.api.info;

				// 验证请求
				if (!crud.service[reqName]) {
					done();
					return Promise.reject(`Request function '${reqName}' is not fount!`);
				}

				// 发送请求
				const res = await crud.service[reqName]({
					id: data.id
				})
					.then((res: any) => {
						done(res);

						// 同步打开表单
						if (props.sync) {
							open();
						}

						return res;
					})
					.catch((err: string) => {
						ElMessage.error(err);
						return Promise.reject(err);
					});

				// 隐藏加载框
				hiddenLoading();

				return res;
			};

			// 获取详情钩子
			if (props.onInfo) {
				props.onInfo(data, {
					next,
					done: (data: any) => {
						done(data);
						emit("opened", true, form);
					},
					close
				});
			} else {
				next(data);
			}
		}

		// 消息事件
		(function () {
			mitt.on("crud.add", add);
			mitt.on("crud.append", append);
			mitt.on("crud.edit", edit);
			mitt.on("crud.close", close);
		})();

		// 共享数据
		provide("upsert", {
			isLoaded,
			isEdit,
			add,
			append,
			close,
			submit
		});

		return {
			refs,
			setRefs,
			form,
			isEdit,
			add,
			append,
			edit,
			close,
			...useFormApi({ refs })
		};
	},

	render(ctx: any) {
		return (
			<div class="cl-upsert">
				{h(
					<cl-form ref={ctx.setRefs("form")} v-model={ctx.form}></cl-form>,
					{},
					ctx.$slots
				)}
			</div>
		);
	}
});
