import emitter from "@/emitter";
import { merge } from "merge";
import { deepMerge, isFunction } from "../../utils";

export const crudList: any[] = [];

export const bootstrap = (crud: any) => {
	const { params, permission, service, refresh, id } = crud || {};

	const app = {
		refresh(d: any) {
			return isFunction(d) ? d(params, refresh) : refresh(d);
		}
	};

	function ctx(data: any) {
		deepMerge(crud, data);

		return ctx;
	}

	ctx.id = id;

	ctx.service = function (s: any) {
		if (s) {
			Object.assign(crud.service, s);
			crud.service.__proto__ = s.__proto__;

			if (s._permission) {
				for (const i in s._permission) {
					crud.permission[i] = s._permission[i];
				}
			}
		}

		return ctx;
	};

	ctx.permission = function (d: any) {
		if (isFunction(d)) {
			merge(permission, d({ service, permission }));
		} else {
			merge(permission, d);
		}

		return ctx;
	};

	ctx.set = (key: string, value: any) => {
		deepMerge(crud[key], value);

		return ctx;
	};

	ctx.on = (name: string, callback: Function) => {
		emitter.on(`${name}-${ctx.id}`, callback);
	};

	ctx.done = function () {
		crudList.push(crud);
	};

	return { ctx, app };
};
