import {
	defineComponent,
	getCurrentInstance,
	inject,
	onMounted,
	onUnmounted,
	provide,
	reactive,
	ref
} from "vue";
import { useMitt, useRequest } from "./helper";
import { bootstrap } from "./app";
import Mitt from "@/utils/mitt";
import { deepMerge } from "@/utils";
import { merge } from "merge";

export default defineComponent({
	name: "cl-crud",

	props: {
		name: String,
		border: Boolean,
		onDelete: Function,
		onRefresh: Function
	},

	emits: ["load"],

	setup(props, { emit }) {
		const ctx = getCurrentInstance();

		// 组件间通讯
		const mitt = new Mitt(ctx?.uid);

		// 配置
		const crud = reactive<any>({
			id: props.name || ctx?.uid,
			routePath: location.pathname || "/",
			dict: {
				api: {
					list: "list",
					add: "add",
					update: "update",
					delete: "delete",
					info: "info",
					page: "page"
				},
				pagination: {
					page: "page",
					size: "size"
				},
				search: {
					keyWord: "keyWord",
					query: "query"
				},
				sort: {
					order: "order",
					prop: "prop"
				},
				label: {
					add: "新增",
					delete: "删除",
					multiDelete: "删除",
					update: "编辑",
					refresh: "刷新",
					advSearch: "高级搜索",
					saveButtonText: "保存",
					closeButtonText: "关闭"
				}
			},
			selection: [],
			table: {
				"context-menu": true
			},
			crudRef: ref<any>({}),
			service: {},
			loading: false,
			params: {
				page: 1,
				size: 20
			},
			permission: {
				update: true,
				page: true,
				info: true,
				list: true,
				add: true,
				delete: true
			}
		});

		// 集合
		merge(crud, useMitt({ mitt }), useRequest({ mitt, props, crud }));

		// 提供
		provide("crud", crud);
		provide("mitt", mitt);

		// 加载
		onMounted(() => {
			// 加载完成回调
			emit("load", bootstrap(deepMerge(crud, inject("crudConf"))));

			// 监听窗口大小改变事件
			function onResize() {
				mitt.emit("crud.resize");
			}

			window.removeEventListener("resize", onResize);
			window.addEventListener("resize", onResize);
		});

		return crud;
	},

	render(ctx: any) {
		return (
			<div class={["cl-crud", { "is-border": ctx.border }]} ref="crudRef">
				{ctx.$slots.default && ctx.$slots.default()}
			</div>
		);
	}
});
