import { defineComponent, h, nextTick, provide, reactive, ref, watch } from "vue";
import cloneDeep from "clone-deep";
import { useAction } from "./helper";
import { useForm, useRefs, useTools } from "@/hooks";
import { deepMerge, isBoolean, isEmpty, isString } from "@/utils";
import Parse from "@/utils/parse";
import { renderNode } from "@/utils/vnode";
import { Form } from "@/types";
import FormHook from "@/hooks/form";

export default defineComponent({
	name: "cl-form",

	props: {
		// 表单值
		modelValue: {
			type: Object,
			default: () => {
				return {};
			}
		},

		// 是否只显示表单
		inner: Boolean,

		// 绑定组件名，设置方法
		bindComponentName: String
	},

	emits: ["update:modelValue"],

	setup(props, { emit }) {
		const { refs, setRefs } = useRefs();
		const { browser } = useTools();

		// 设置表单值
		const { setFormData } = useForm(props);

		// 表单是否可见
		const visible = ref<boolean>(false);

		// 表单提交保存状态
		const saving = ref<boolean>(false);

		// 选项卡
		const tabActive = ref<any>(null);

		// 表单数据
		const form = setFormData();

		// 表单配置
		const conf = reactive<Form>({
			title: "自定义表单",
			width: "50%",
			props: {
				size: "small",
				labelWidth: "100px"
			},
			on: {},
			op: {
				hidden: false,
				saveButtonText: "保存",
				closeButtonText: "取消",
				buttons: ["close", "save"]
			},
			dialog: {
				props: {
					fullscreen: false,
					"close-on-click-modal": false,
					"append-to-body": true
				},
				hiddenControls: false,
				controls: ["fullscreen", "close"]
			},
			items: [],
			_data: {}
		});

		// 表单动作
		const {
			loading,
			showLoading,
			hiddenLoading,
			collapseItem,
			getForm,
			setForm,
			setData,
			setOptions,
			setProps,
			toggleItem,
			hiddenItem,
			showItem,
			resetFields,
			clearValidate,
			validateField,
			validate
		} = useAction({ conf, form, refs });

		// 更新绑定值
		watch(form, (val: any) => {
			emit("update:modelValue", val);
		});

		// 提供
		provide("form", form);

		// 旧数据
		let defForm: any = null;

		// 重置表单值
		function reset() {
			// 清除验证
			clearValidate();
		}

		// 请求表单保存状态
		function done() {
			saving.value = false;
		}

		// 关闭表单
		function close() {
			visible.value = false;
			done();
		}

		function onClosed() {
			tabActive.value = null;
			reset();
		}

		// 表单关闭前事件
		function beforeClose() {
			if (conf.on?.close) {
				conf.on.close(close);
			} else {
				close();
			}
		}

		// 清空表单验证
		function clear() {
			for (const i in form) {
				delete form[i];
			}

			clearValidate();
		}

		// 表单提交
		function submit(callback?: Function) {
			// 验证表单
			refs.value.form.validate(async (valid: boolean, error: any) => {
				if (valid) {
					saving.value = true;

					// 拷贝表单值
					const d = cloneDeep(form);

					// 过滤隐藏的表单项
					conf.items.forEach((e: any) => {
						if (e._hidden) {
							delete d[e.prop];
						}

						if (e.hook) {
							d[e.prop] = FormHook.submit({
								...e,
								value: d[e.prop],
								form: d
							});
						}
					});

					// 处理 "-" 多层级
					for (const i in d) {
						if (i.includes("-")) {
							// 结构参数
							const [a, ...arr] = i.split("-");

							// 关键值的key
							const k: string = arr.pop() || "";

							if (!d[a]) {
								d[a] = {};
							}

							let f: any = d[a];

							// 设置默认值
							arr.forEach((e: any) => {
								if (!f[e]) {
									f[e] = {};
								}

								f = f[e];
							});

							// 设置关键值
							f[k] = d[i];

							delete d[i];
						}
					}

					const submit = callback || conf.on?.submit;

					// 提交事件
					if (submit) {
						submit(d, {
							done,
							close
						});
					} else {
						console.error("Not found callback function");
						done();
					}
				} else {
					// 判断是否使用form-tabs，切换到对应的选项卡
					const keys = Object.keys(error);

					if (tabActive.value) {
						const item = conf.items.find((e) => e.prop === keys[0]);

						if (item) {
							tabActive.value = item.group;
						}
					}
				}
			});
		}

		// 打开表单
		function open(options?: Form) {
			if (!options) {
				options = {
					items: []
				};
			}

			// 是否重置
			if (options.isReset !== false) {
				if (defForm) {
					for (const i in form) {
						if (defForm[i] !== undefined) {
							form[i] = cloneDeep(defForm[i]);
						} else {
							delete form[i];
						}
					}
				}
			}

			// 显示对话框
			visible.value = true;

			// 合并配置
			for (const i in conf) {
				switch (i) {
					case "items":
						conf.items = cloneDeep(options.items || []);
						break;
					case "title":
					case "width":
						conf[i] = options[i];
						break;
					case "props":
					case "on":
					case "op":
					case "dialog":
					case "_data":
						conf[i] = deepMerge(conf[i], options[i] || {});
						break;
				}
			}

			// 预设表单值
			if (options?.form) {
				for (const i in options.form) {
					form[i] = options.form[i];
				}
			}

			// 分组
			const tabs: any = conf.items.find((e: any) => e.type === "tabs");
			const { mergeProp, labels = [] } = tabs?.props || {};

			// 设置表单数据
			conf.items.map((e: any) => {
				if (e.prop) {
					// 解析 prop
					if (e.prop.includes(".")) {
						e.prop = e.prop.replace(/\./g, "-");
					}

					// prop 合并
					if (mergeProp) {
						const t = labels.find((s: any) => s.value == e.group);

						if (t && t.name) {
							e.prop = `${t.name}-${e.prop}`;
						}
					}

					// 绑定值
					form[e.prop] = FormHook.bind({
						...e,
						value: form[e.prop] !== undefined ? form[e.prop] : cloneDeep(e.value),
						form
					});

					// 标题
					if (isString(e.label)) {
						e._label = {
							text: e.label
						};
					} else {
						e._label = e.label;
					}

					// 表单验证
					if (e.required) {
						e.rules = {
							required: true,
							message: `${e._label.text}不能为空`
						};
					}
				}

				// 设置 tabs 默认值
				if (e.type == "tabs") {
					tabActive.value = e.value;
				}
			});

			// 设置默认值
			if (!defForm) {
				defForm = cloneDeep(form);
			}

			// 打开回调
			nextTick(() => {
				if (conf.on?.open) {
					conf.on.open(form, {
						close,
						submit,
						done
					});
				}
			});

			return {
				showLoading,
				hiddenLoading,
				collapseItem,
				getForm,
				setForm,
				setData,
				setOptions,
				setProps,
				toggleItem,
				hiddenItem,
				showItem,
				resetFields,
				clearValidate,
				validateField,
				validate
			};
		}

		// 重新绑定表单数据
		function reBindForm(data: any) {
			const d: any = {};

			conf.items.forEach((e: any) => {
				d[e.prop] = e.hook
					? FormHook.bind({
							...e,
							value: data[e.prop],
							form: data
					  })
					: data[e.prop];
			});

			Object.assign(form, data, d);
		}

		// 设置标题
		function setTitle(value: string) {
			conf.title = value;
		}

		// 切换 cl-form-tab
		function changeTab(value: any, isValid: boolean = true) {
			return new Promise((resolve: Function, reject: Function) => {
				function next() {
					tabActive.value = value;
					resolve();
				}

				if (isValid) {
					let isError: boolean = false;

					const arr = conf.items
						.filter((e: any) => e.group == tabActive.value && !e._hidden && e.prop)
						.map((e: any) => {
							return new Promise((r: Function) => {
								// 验证表单
								refs.value.form.validateField(e.prop, (valid: string) => {
									if (valid) {
										isError = true;
									}

									r(valid);
								});
							});
						});

					Promise.all(arr).then((msg) => {
						if (isError) {
							reject(msg.filter(Boolean));
						} else {
							next();
						}
					});
				} else {
					next();
				}
			});
		}

		return {
			browser,
			visible,
			saving,
			tabActive,
			form,
			refs,
			setRefs,
			conf,
			loading,
			open,
			beforeClose,
			close,
			onClosed,
			done,
			clear,
			submit,
			reBindForm,
			showLoading,
			hiddenLoading,
			collapseItem,
			getForm,
			setForm,
			setData,
			setOptions,
			setProps,
			toggleItem,
			hiddenItem,
			showItem,
			resetFields,
			clearValidate,
			validateField,
			validate,
			changeTab,
			setTitle
		};
	},

	render(ctx: any) {
		const { props, op, title, width, dialog, _data } = ctx.conf;

		// 渲染表单及表单项
		const renderForm = () => {
			// 表单项列表
			const children = ctx.conf.items.map((e: any) => {
				if (e.type == "tabs") {
					return <cl-form-tabs v-model={ctx.tabActive} {...e.props}></cl-form-tabs>;
				}

				// 隐藏处理
				e._hidden = Parse("hidden", {
					value: e.hidden,
					scope: ctx.form,
					data: _data
				});

				// 分组
				e._group =
					isEmpty(ctx.tabActive) || isEmpty(e.group) ? true : e.group === ctx.tabActive;

				// Flex handler
				if (isEmpty(e.flex)) {
					e._flex = true;
				}

				return (
					!e._hidden && (
						<el-col span={24} {...e}>
							{e.component &&
								h(
									<el-form-item v-show={e._group}></el-form-item>,
									{
										prop: e.prop,
										rules: e.rules,
										...e.props
									},
									{
										label: () => {
											return (
												<el-tooltip
													effect="dark"
													placement="top"
													content={e._label?.tip}
													disabled={!e._label?.tip}>
													<span>
														{e._label?.text}
														{e._label?.icon && (
															<i class={e._label?.icon}></i>
														)}
													</span>
												</el-tooltip>
											);
										},
										default: () => {
											return (
												<div>
													{/* Form item */}
													<div class="cl-form-item">
														{["prepend", "component", "append"].map(
															(name) => {
																return (
																	e[name] && (
																		<div
																			v-show={!e.collapse}
																			class={[
																				`cl-form-item__${name}`,
																				{
																					"is-flex":
																						e._flex
																				}
																			]}>
																			{renderNode(e[name], {
																				prop: e.prop,
																				scope: ctx.form,
																				slots: ctx.$slots,
																				_data
																			})}
																		</div>
																	)
																);
															}
														)}
													</div>
													{/* Collapse button */}
													{isBoolean(e.collapse) && (
														<div
															class="cl-form-item__collapse"
															onClick={() => {
																ctx.collapseItem(e);
															}}>
															<el-divider content-position="center">
																{e.collapse ? (
																	<span>
																		查看更多
																		<i class="el-icon-arrow-down"></i>
																	</span>
																) : (
																	<span>
																		隐藏内容
																		<i class="el-icon-arrow-up"></i>
																	</span>
																)}
															</el-divider>
														</div>
													)}
												</div>
											);
										}
									}
								)}
						</el-col>
					)
				);
			});

			// el-form
			const ElForm = (
				<el-form
					ref={ctx.setRefs("form")}
					label-position={ctx.browser.isMini ? "top" : ""}
					size="small"
					label-width="100px"
					disabled={ctx.saving}
					model={ctx.form}></el-form>
			);

			return h(ElForm, props, {
				default: () => {
					return (
						<el-row gutter={10} v-loading={ctx.loading}>
							{children}
						</el-row>
					);
				}
			});
		};

		// 渲染表单按钮
		function renderFooter() {
			const { hidden, buttons, saveButtonText, closeButtonText } = op;
			const { size = "small" } = props;

			return hidden
				? null
				: buttons.map((vnode: any) => {
						if (vnode == "save") {
							return (
								<el-button
									{...{
										size,
										type: "success",
										disabled: ctx.loading,
										loading: ctx.saving,
										onClick: () => {
											ctx.submit();
										}
									}}>
									{saveButtonText}
								</el-button>
							);
						} else if (vnode == "close") {
							return (
								<el-button
									{...{
										size,
										onClick: () => {
											ctx.beforeClose();
										}
									}}>
									{closeButtonText}
								</el-button>
							);
						} else {
							return renderNode(vnode, {
								scope: ctx.form,
								slots: ctx.$slots,
								type: "form-op"
							});
						}
				  });
		}

		const Form = (
			<div class="cl-form">
				<div class="cl-form__container">{renderForm()}</div>
				<div class="cl-form__footer">{renderFooter()}</div>
			</div>
		);

		if (ctx.inner) {
			return ctx.visible && Form;
		} else {
			return h(
				<cl-dialog v-model={ctx.visible}></cl-dialog>,
				{
					title,
					width,
					...dialog,
					props: {
						...dialog.props,
						"before-close": ctx.beforeClose
					},
					onClosed: ctx.onClosed
				},
				{
					default() {
						return Form;
					}
				}
			);
		}
	}
});
