import { useCore } from "@/hooks";
import { defineComponent, ref, watch } from "vue";
import { isArray } from "../utils";

export default defineComponent({
	name: "cl-query",

	props: {
		modelValue: null,
		list: {
			type: Array,
			required: true
		},
		field: {
			type: String,
			default: "query"
		},
		multiple: Boolean,
		callback: Function
	},

	emits: ["update:modelValue", "change"],

	setup(props, { emit }) {
		const { crud } = useCore();

		const list2 = ref<Array<any>>([]);

		// 更新数据列表
		const update = () => {
			let arr: Array<any> = [];

			if (isArray(props.modelValue)) {
				arr = props.modelValue;
			} else {
				arr = [props.modelValue];
			}

			if (!props.multiple) {
				arr.splice(1);
			}

			// 默认选择
			list2.value = (props.list || []).map((e: any) => {
				e.active = arr.some((v) => v === e.value);
				return e;
			});
		};

		update();

		// 点击选择项
		const selectItem = (event: any, item: any) => {
			if (item.active) {
				item.active = false;
			} else {
				if (props.multiple) {
					item.active = true;
				} else {
					list2.value.map((e: any) => {
						e.active = e.value == item.value;
					});
				}
			}

			// 过滤未选中的
			const selection = list2.value.filter((e: any) => e.active).map((e: any) => e.value);
			// 处理多选情况
			const value = props.multiple ? selection : selection[0];

			// 请求回调
			if (props.callback) {
				props.callback(value);
			} else {
				crud.refresh({
					[props.field]: value
				});

				emit("change", value);
			}

			// 阻止默认事件
			event.preventDefault();
		};

		// 监听绑定值，更新数据列表
		watch(
			() => props.modelValue,
			() => {
				update();
			}
		);

		return {
			list2,
			selectItem
		};
	},

	render(ctx: any) {
		return (
			<div class="cl-query">
				{ctx.list2.map((item: any, index: number) => {
					return (
						<button
							class={{ "is-active": item.active }}
							key={index}
							onClick={(event) => {
								ctx.selectItem(event, item);
							}}>
							<span>{item.label}</span>
						</button>
					);
				})}
			</div>
		);
	}
});
